export const keyboardListeners = (game) => {
  document.addEventListener("keypress", (event) => {
    switch (event.keyCode) {
      case (event.keyCode === 32) && (game.gameOver !== true):
        event.preventDefault();
        if (game.gameState === "pause") {
          game.showTitles = false;
          game.play();
        } else if (game.gameState === "play") {
          game.pause();
        }
        break;
      case 82:
        game.resetGame();
        break;
      default:
        break;
    }
  });
};

export const formatScore = (score) => {
  if (score > 99999) {
    return '99999';
  } else if (score > 9999) {
    return score;
  } else if (score > 999) {
    return '0' + score;
  } else if (score > 99) {
    return '00' + score;
  } else if (score > 9) {
    return '000' + score;
  } else {
    return '0000' + score;
  }
};



